    flex-wrap: wrap;
    justify-content: center;
}

.hero-img-container {
    margin: 2rem;
    position: relative;
}

.hero-img {
    min-width: 6rem;
    height: auto;
    box-shadow: 2px 2px 5px rgba(0, 0, 0, 0.3);
}

.hero-name {
    position: absolute;
