    max-width: 30rem;
    display: flex;
    flex-direction: column;
    justify-content: center;
}

.about-text {
    margin-bottom: 1rem;
}

.hero-icons-container {
    align-self: last baseline;
    display: flex;
    gap: 0.5rem;
    padding-right: 1rem;
}

.hero-icon {
    width: 2rem;
    height: auto;
}

.hero-icon:hover {
    cursor: pointer;
}

/* Projects Section */
.projects-section {
    margin: 2rem;
}

.projects-header {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-bottom: 1rem;
}

.projects-container {
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
    gap: 1rem;
}

.project-card {
    box-shadow: 2px 2px 5px rgba(0, 0, 0, 0.3);
}

.project-img {
    object-fit: cover;
    min-width: 100%;
}

.project-header-container {
    display: flex;
    justify-content: space-between;
}

.project-title {
    padding-top: 0.5rem;
    padding-left: 1rem;
}

.project-icons-container {
    display: flex;
    gap: 0.5rem;
    align-items: center;
    padding-right: 1rem;
}

.project-icons-container:hover {
    cursor: pointer;
}

.project-icon {
    width: 1.5rem;
    height: 1.5rem;
}

.project-description {
    padding: 0 1rem 1rem 1rem;
}

/* Footer Styling */
footer {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    background-color: var(--teal);
    width: 100vw;
    max-width: 100%;
}

.footer-text-content {
    display: flex;
