}

.footer-header {
    color: white;
    font-size: 3rem;
    align-self: center;
}

.footer-text {
    color: white;
    line-height: 1.3;
    margin-bottom: 1rem;
    font-size: 1.2rem;
}

.footer-address {
    color: white;
}

.telephone-container {
    display: flex;
    gap: 0.5rem;
    align-items: center;
    margin-top: 1rem;
    margin-bottom: 1rem;
}

.telephone-icon {
    width: 1rem;
    height: 1re;
}

.telephone-number {
    color: white;
}

.email-container {
    display: flex;
    gap: 0.5rem;
    align-items: center;
    margin-bottom: 1rem;
}

.email-icon {
    width: 1rem;
    height: 1rem;
}

.email {
    color: white;
}

.footer-icons-container {
    display: flex;
    gap: 1rem;
    align-self: center;
}

.footer-icon {
    width: 2rem;
    height: 2rem;
    cursor: pointer;
}

.footer-image {
    min-width: 6rem;
    height: auto;
}

